.popups{
  position: relative;
  width: 100vw;
  height: 100vh;
  background-color: rgb(97, 97, 189);
  display: flex;
  align-items: center;
  justify-content: center;
}
.popups button{
  display: inline-block;
  padding: 1rem 5rem;
  font-size: 1.3rem;
  font-weight: bold;
  letter-spacing: .5px;
  border-radius: 30px;
  border: none;
  cursor: pointer;
}
.popup{
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgb(0, 0, 0,30%);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: .5s;
  opacity: 0;
  z-index: -1;
}
.popup_wrapper{
  position: relative;
  width: 400px;
  background-color: white;
  border-radius: 10px;
  padding: 2rem;
  box-sizing: border-box;
  text-align: center;

}
.popup_wrapper .popup_icon{
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: -3rem;
  font-size: 6rem;
  color: rgb(255, 255, 255);
  background-color: rgb(97, 97, 189);
  border-radius: 100%;
}
.popup_wrapper h1{
  margin-top: 3rem;
  margin-bottom: 0;
  font-size: 2.5rem;
  font-weight: 400;
}
.popup_wrapper p{
  margin: .5rem 0 2.5rem;
  font-size: 1.2rem;
}
.popup_wrapper button{
  background-color: rgb(39, 59, 162);
  width: 100%;
  font-size:1rem;
  font-weight: bold;
  color: white;
}